#header {
  position: fixed;
  z-index: 900;
  top: 0;
  right: 0;
  left: 0;
  display: grid;
  place-items: center;
  width: 100vw;
  height: 5rem;
  background-color: #ffffff;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1);
  border-top: 10px solid #001532;
