  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header__menu,
.menu__items {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.menu__item {
  height: 100%;
