#hero {
  background-color: #245998;
  background-image: linear-gradient(to top right, #001532, #245998);
}
#hero .container, #hero .container--header {
  position: relative;
  z-index: 100;
}
#hero::after {
  content: "";
  position: absolute;
  z-index: 25;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("static/img/kerby-court.webp");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  filter: saturate(0%) brightness(80%);
  mix-blend-mode: overlay;
}

.hero__wrapper, .cta__wrapper, .care__wrapper, .optin__wrapper, .success__wrapper {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-template-rows: repeat(auto, minmax(0, min-content));
  gap: 3.75rem;
}
@media screen and (min-width: 64rem) {
  .hero__wrapper, .cta__wrapper, .care__wrapper, .optin__wrapper, .success__wrapper {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-rows: repeat(1, minmax(0, 1fr));
  }
}

.hero__heading, .cta__heading, .intro__heading, .care__heading, .optin__heading, .success__heading {
  color: #ffffff;
  margin-bottom: 0.625rem;
}
@media screen and (min-width: 64rem) {
