#cta {
  background-color: #245998;
  background-image: linear-gradient(to top right, #001532, #245998);
  overflow: hidden;
}
#cta > * {
  position: relative;
  z-index: 100;
}
#cta::before {
  content: "";
  position: absolute;
  z-index: 50;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("static/img/humblestrong-logo-white.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  transform: rotate(-5deg);
  mix-blend-mode: screen;
  opacity: 0.1;
}
#cta::after {
  content: "";
  position: absolute;
  z-index: 25;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("static/img/kerby-turf.webp");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  filter: saturate(0%);
